.container {
    display: flex;
    flex-direction: column;
    padding: 0.01em 0;
    margin-top: 5em;
    width: 282px;
    background-color: var(--verde);
    box-shadow: 4px 4px 4px rgba(0, 0, 0, .25);
    margin: 16px;
}

.capa {
    width: 100%;
    justify-content: space-around;
}

.container h2, .favorito {
    padding: 0 1em;
}

.favorito {
    margin-left: 10px;
    width: 25px;
}

.link {
    text-decoration: none;
    color: inherit;
    text-align: left;
}